.brands {
  background-color: #f8f8f8;
  margin-bottom: 16px; }

.brands__title {
  text-transform: uppercase;
  font-size: 16px;
  line-height: 24px;
  color: #7e7e82;
  display: block;
  background: -webkit-gradient(linear, left top, right top, from(#f8f8f8), color-stop(98.61%, #ffffff));
  background: -o-linear-gradient(left, #f8f8f8 0%, #ffffff 98.61%);
  background: linear-gradient(90deg, #f8f8f8 0%, #ffffff 98.61%);
  padding: 16px;
  margin-bottom: 24px; }

.brands__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 100px;
  overflow: hidden;
  list-style: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.brands__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 240px;
  height: 72px;
  padding-left: 16px;
  padding-right: 16px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  background-color: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  margin-bottom: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

@media screen and (max-width: 320px) {
  .brands__container {
    max-width: 320px;
    overflow-x: scroll;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    /*align-items: center;*/
    height: 80px;
    margin-bottom: 16px;
    background-color: lightblue;
    padding: 16px;
    padding-left: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .brands__title {
    white-space: normal;
    max-width: 320px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .brands__item {
    width: 240px;
    height: 72px;
    margin-left: 16px;
    margin-right: 0; }
  .brands__link--open {
    display: none; } }

@media screen and (min-width: 321px) {
  .brands__title {
    padding-left: 24px;
    white-space: normal;
    width: auto; }
  .brands__container {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    width: 816px;
    height: 156px;
    margin-bottom: 8px;
    overflow-x: visible;
    overflow-y: hidden;
    background-color: blue;
    padding-right: 24px;
    margin-left: auto;
    margin-right: auto; }
  .brands__item {
    width: 240px;
    height: 72px;
    margin-bottom: 16px;
    margin-left: 24px; }
  .brands__link--open {
    display: block; } }

@media screen and (min-width: 1120px) {
  .brands__title {
    padding-left: 32px; }
  .brands__container {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 1120px;
    height: 176px;
    margin-bottom: 8px;
    overflow: hidden;
    background-color: darkblue;
    padding-right: 0; }
  .brands__item {
    width: 240px;
    height: 72px;
    margin-bottom: 16px;
    margin-left: 32px; }
    .brands__item:nth-of-type(3n) {
      margin-right: 0px; }
  /*.brands__item:nth-of-type(4n){       
        margin-left: 32px;
    }*/ }

.brands__link--hide {
  display: none;
  border: 1px solid red; }

.arrow-btn {
  display: block;
  background: url(./static/gob_6.svg) no-repeat;
  width: 42px;
  height: 42px; }
